<template>
    <div class="alarm-info-box">
        <div class="toolbar-box">
            <span>关键参数 : </span>
            <div class="layoutBox" style="width:160px">
                <el-select v-model="parameter" multiple="multiple" collapse-tags placeholder="请选择">
                    <el-option
                        v-for="item in optionData"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </div>&emsp;
            <span>时间参数 : </span>
            <div class="layoutBox" style="width:242px">
                <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
            </div>&emsp;
            <el-button><i class="iconfont icon-sousuo"></i>查询</el-button>
        </div>
        <div class="tip-text-box">
            <i class="iconfont icon-tishi"></i>
            <span>温馨提示：所选时间段内，共有该点位报警记录9条</span>
            <span class="toolbar-btn">生成任务 <i class="el-icon-d-arrow-right"></i></span>
        </div>
        <div class="info-content-wrap">
            <div class="left-content">
                <ul class="nav-list">
                    <li></li>
                    <li v-for="(item,index) in navList" :key="index+'w'" :class="{active:navActive === index}">
                        <span @click="jumpAnchor(index)">{{ item.name }}</span>
                    </li>
                    <li></li>
                </ul>
            </div>
            <div class="right-content">
                <el-scrollbar style="height: 100%;" ref="scroll">
                    <div class="content-box">
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>基础信息</span></div>
                            <div class="modular-content">
                                <ul class="base-info-list clear">
                                    <li>
                                        <span class="label">报警站点：</span>
                                        <span class="value">长沙市-生米</span>
                                    </li>
                                    <li>
                                        <span class="label">所属企业：</span>
                                        <span class="value">宇星科技有限公司</span>
                                    </li>
                                    <li>
                                        <span class="label">报警开始时间：</span>
                                        <span class="value">2018-10-10 10:00:00</span>
                                    </li>
                                    <li>
                                        <span class="label">报警类型：</span>
                                        <span class="value">超标报警</span>
                                    </li>
                                    <li>
                                        <span class="label">报警项目：</span>
                                        <span class="value">因子超标</span>
                                    </li>
                                    <li>
                                        <span class="label">报警结束时间：</span>
                                        <span class="value">2018-10-10 10:00:00</span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>趋势曲线</span></div>
                            <div class="modular-content">
                                <div id="trend-curve-chart"></div>
                            </div>
                        </div>
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>报警信息</span></div>
                            <div class="modular-content" style="padding: 8px;height:350px;">
                                <el-table
                                    :data="alarmInfoData"
                                    border
                                    height="300px"
                                    style="width: 100%">
                                    <el-table-column
                                        prop="alarmType"
                                        label="报警类型">
                                    </el-table-column>
                                    <el-table-column
                                        prop="monitoringProjects"
                                        label="监测项目">
                                    </el-table-column>
                                    <el-table-column
                                        prop="startTime"
                                        :show-overflow-tooltip = 'true'
                                        label="报警开始时间">
                                    </el-table-column>
                                    <el-table-column
                                        prop="endTime"
                                        :show-overflow-tooltip = 'true'
                                        label="报警结束时间">
                                    </el-table-column>
                                    <el-table-column
                                        prop="currentState"
                                        label="当前状态">
                                    </el-table-column>
                                    <el-table-column
                                        prop="auditor"
                                        label="审核人">
                                    </el-table-column>
                                     <el-table-column
                                        prop="auditTime"
                                        :show-overflow-tooltip = 'true'
                                        label="审核时间">
                                    </el-table-column>
                                     <el-table-column
                                        prop="diagnosticCauses"
                                        label="诊断原因">
                                    </el-table-column>
                                </el-table>
                                <div class="paginationBox">
                                    <el-pagination
                                    @size-change="alarmInfoSizeChange"
                                    @current-change="alarmInfoCurrentChange"
                                    :current-page.sync="currentPageAR"
                                    :page-size="pageSizeAR"
                                    background
                                    :small = "true"
                                    popper-class = "select-common-option"
                                    :page-sizes = "pageListAR"
                                    layout="total,sizes,slot,->,prev, pager, next, jumper"
                                    :total="totalRecordsAR">
                                    <span class="el-pagination__current">当前第{{currentPageAR}}/{{totalPageAR}}页</span>
                                    </el-pagination>
                                </div>
                            </div>
                        </div>
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>监测数据</span></div>
                            <div class="modular-content" style="padding: 8px;">
                                <div class="modular-toolbar-wrap">
                                    <span>时间选择 : </span>
                                    <div class="layoutBox" style="width:242px">
                                        <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
                                    </div>&emsp;
                                    <el-button><i class="iconfont icon-sousuo"></i>查询</el-button>
                                </div>
                                <div class="table-wrap" style="height:300px;">
                                    <el-table
                                        :data="monitoringData"
                                        border
                                        height="300px"
                                        style="width: 100%">
                                        <el-table-column
                                            label="监测时间">
                                            <el-table-column
                                                prop="monitoringTime"
                                                width="164px"
                                                label="Ⅲ类标准限值">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="水温(℃)">
                                            <el-table-column
                                                prop="factorA"
                                                label="">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="PH(无量纲)">
                                            <el-table-column
                                                prop="factorB"
                                                :show-overflow-tooltip = 'true'
                                                label="6~9">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="溶解氧(mg/l)">
                                            <el-table-column
                                                prop="factorC"
                                                :show-overflow-tooltip = 'true'
                                                label="≥5">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="电导率(μs/cm)">
                                            <el-table-column
                                                prop="factorD"
                                                :show-overflow-tooltip = 'true'
                                                label="">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="浊度(NTU)">
                                            <el-table-column
                                                prop="factorE"
                                                :show-overflow-tooltip = 'true'
                                                label="">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="高锰酸盐指数(mg/l)">
                                             <el-table-column
                                                prop="factorF"
                                                width="124px"
                                                :show-overflow-tooltip = 'true'
                                                label="6">
                                            </el-table-column>
                                        </el-table-column>
                                        <el-table-column
                                            label="氨氮(mg/l)">
                                            <el-table-column
                                                prop="factorH"
                                                :show-overflow-tooltip = 'true'
                                                label="1">
                                            </el-table-column>
                                        </el-table-column>
                                    </el-table>
                                </div>
                                <div class="paginationBox">
                                    <el-pagination
                                    @size-change="monitorinSizeChange"
                                    @current-change="monitorinCurrentChange"
                                    :current-page.sync="currentPageJC"
                                    :page-size="pageSizeJC"
                                    background
                                    :small = "true"
                                    popper-class = "select-common-option"
                                    :page-sizes = "pageListJC"
                                    layout="total,sizes,slot,->,prev, pager, next, jumper"
                                    :total="totalRecordsJC">
                                    <span class="el-pagination__current">当前第{{currentPageJC}}/{{totalPageJC}}页</span>
                                    </el-pagination>
                                </div>
                            </div>
                        </div>
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>门禁记录</span></div>
                            <div class="modular-content" style="padding: 8px;height:350px;">
                                <el-table
                                    :data="entranceGuardData"
                                    border
                                    height="300px"
                                    style="width: 100%">
                                    <el-table-column
                                        type="index"
                                        label="序号">
                                    </el-table-column>
                                    <el-table-column
                                        prop="cardNumber"
                                        label="卡号">
                                    </el-table-column>
                                    <el-table-column
                                        prop="creditCardSwipeStaff"
                                        label="刷卡员工">
                                    </el-table-column>
                                    <el-table-column
                                        prop="cardSwipeTime"
                                        :show-overflow-tooltip = 'true'
                                        label="刷卡时间">
                                    </el-table-column>
                                    <el-table-column
                                        prop="controlPhotos"
                                        label="门禁照片">
                                    </el-table-column>
                                    <el-table-column
                                        prop="remarks"
                                        label="备注">
                                    </el-table-column>
                                </el-table>
                                <div class="paginationBox">
                                    <el-pagination
                                    @size-change="recordsSizeChange"
                                    @current-change="recordsCurrentChange"
                                    :current-page.sync="currentPageMJ"
                                    :page-size="pageSizeMJ"
                                    background
                                    :small = "true"
                                    popper-class = "select-common-option"
                                    :page-sizes = "pageListMJ"
                                    layout="total,sizes,slot,->,prev, pager, next, jumper"
                                    :total="totalRecordsMJ">
                                    <span class="el-pagination__current">当前第{{currentPageMJ}}/{{totalPageMJ}}页</span>
                                    </el-pagination>
                                </div>
                            </div>
                        </div>
                        <div class="modular-box">
                            <div class="modular-head modular-point"><span>远程返控</span></div>
                            <div class="modular-content">
                                <el-scrollbar style="width: 100%;margin-bottom: 4px;">
                                    <div style="padding: 8px;width:1300px;height:500px;padding-bottom: 20px;">
                                        <newRemoteControl></newRemoteControl>
                                    </div>
                                </el-scrollbar>
                            </div>
                        </div>
                    </div>
                </el-scrollbar>
            </div>
        </div>
    </div>
</template>
<script>
import newRemoteControl from '../../qualityControl/newRemoteControl'
export default {
    name:'alarmInfo',
    components:{
        newRemoteControl
    },
    data(){
        return{
            parameter:'',
            optionData:[
                {
                    label:'水温',
                    value:'w1',
                },
                {
                    label:'PH',
                    value:'w2',
                },
                {
                    label:'溶解氧',
                    value:'w3',
                }
            ],
            dateRange:'',
            navList:[{
                name: "基础信息"
            },{
                name: "趋势曲线"
            },{
                name: "报警信息"
            },{
                name: "监测数据"
            },{
                name: "门禁记录"
            },{
                name: "远程返控"
            }],
            navActive: 0,
            pList:[],
            alarmInfoData:[{
                alarmType:'设备故障',
                monitoringProjects:'项目1',
                startTime:'2018-10-12 18:00:00',
                endTime:'2019-10-12 18:00:00',
                currentState:'已处理',
                auditor:'王五',
                auditTime:'2019-10-12 18:00:00',
                diagnosticCauses:'-',
            },{
                alarmType:'设备故障',
                monitoringProjects:'项目1',
                startTime:'2018-10-12 18:00:00',
                endTime:'2019-10-12 18:00:00',
                currentState:'已处理',
                auditor:'王五',
                auditTime:'2019-10-12 18:00:00',
                diagnosticCauses:'-',
            }],
            // 报警信息分页数据
            currentPageAR: 1, //分页组件当前页
            pageSizeAR: 10, //分页每页多少条数据
            totalRecordsAR: 1000, //总条数
            pageListAR: [10, 15, 20], //设置每页多少条

            //监测数据
            monitoringData:[{
                monitoringTime:' 2019-08-14 14:12:00',
                factorA:'8.8',
                factorB:'8.8',
                factorC:'8.8',
                factorD:'8.8',
                factorE:'8.8',
                factorF:'8.8',
                factorH:'8.8'
            }],
            // 监测数据分页数据
            currentPageJC: 1, //分页组件当前页
            pageSizeJC: 10, //分页每页多少条数据
            totalRecordsJC: 1000, //总条数
            pageListJC: [10, 15, 20], //设置每页多少条

            //门禁记录
            entranceGuardData:[{
                cardNumber:'20135345614',
                creditCardSwipeStaff:'王明',
                cardSwipeTime:'2019-08-14 16:09:10',
                controlPhotos:'-',
                remarks:'-',
            }],
            // 门禁记录分页数据
            currentPageMJ: 1, //分页组件当前页
            pageSizeMJ: 10, //分页每页多少条数据
            totalRecordsMJ: 1000, //总条数
            pageListMJ: [10, 15, 20], //设置每页多少条
        }
    },
    computed: {
      // 报警信息分页总页数
      totalPageAR() {
        let number = 0;
        number = Math.ceil(this.totalRecordsAR / this.pageSizeAR);
        return number;
      },
      // 监测数据分页总页数
      totalPageJC() {
        let number = 0;
        number = Math.ceil(this.totalRecordsJC / this.pageSizeJC);
        return number;
      },
      // 门禁记录分页总页数
      totalPageMJ() {
        let number = 0;
        number = Math.ceil(this.totalRecordsMJ / this.pageSizeMJ);
        return number;
      }
    },
    methods:{
        trendCurveChart(){
          let chart = this.$echarts.init(document.getElementById('trend-curve-chart'));
          let option = {
            color:['#297eff','#1cad8b','#c2a100','#ff4242'],
            tooltip: {
              trigger: 'axis'
            },
            legend: {
              data:['化学需氧量（mg/l）','化学需氧量标准','COD消解时长（min）','COD消解温度（℃）'],
              top: 10
            },
            grid: {
              top: 40,
              left: '3%',
              right: '4%',
              bottom: 50,
              containLabel: true
            },
            dataZoom: [
                {
                    show: true,
                    realtime: true,
                    start: 0,
                    end: 100,
                },
                {
                    type: 'inside',
                    realtime: true,
                    start: 0,
                    end: 100,
                }
            ],
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: ['2019/02/07 05时','2019/02/07 06时','2019/02/07 07时','2019/02/07 08时','2019/02/07 09时',
              '2019/02/07 10时','2019/02/07 11时'],
              axisLine:{
                show: false,
              },
              axisTick:{
                show: false,
              }
            },
            yAxis: {
              type: 'value',
              axisLine:{
                show: false,
              },
              axisTick:{
                show: false,
              }
            },
            series: [
              {
                name:'化学需氧量（mg/l）',
                symbolSize: 8,
                type:'line',
                smooth: true,
                data:[120, 132, 101, 134, 90, 230, 210]
              },
              {
                name:'化学需氧量标准',
                symbolSize: 8,
                type:'line',
                smooth: true,
                data:[220, 182, 191, 234, 290, 330, 310]
              },
              {
                name:'COD消解时长（min）',
                symbolSize: 8,
                type:'line',
                smooth: true,
                data:[150, 232, 201, 154, 190, 330, 410]
              },
              {
                name:'COD消解温度（℃）',
                symbolSize: 8,
                type:'line',
                smooth: true,
                data:[130, 142, 181, 144, 190, 310, 394]
              }
            ]
          };
          chart.setOption(option);
        },
        // 报警信息分页事件
        alarmInfoSizeChange(val){
            this.pageSizeAR = val;
        },
        alarmInfoCurrentChange(val){
             this.currentPageAR = val;
        },
        // 监测数据分页事件
        monitorinSizeChange(val){
            this.pageSizeJC = val;
        },
        monitorinCurrentChange(val){
            this.currentPageJC = val;
        },
        //门禁记录
        recordsSizeChange(val){
            this.pageSizeMJ = val;
        },
        recordsCurrentChange(val){
            this.currentPageMJ = val;
        },
        jumpAnchor(index){
            let {scroll} = this.$refs;
            let scrollbar = scroll.$el;
            let scrollWrap = scrollbar.querySelector('.el-scrollbar__wrap');
            scrollWrap.scrollTop = (this.pList[index]);
            this.navActive = index;
        },
        //获取节点坐标
        nodeCoordinates(){
            let alarm = document.querySelector('.alarm-info-box');
            let point = alarm.querySelectorAll('.modular-point');
            let {scroll} = this.$refs;
            let scrollbar = scroll.$el;
            let scrollWrap = scrollbar.querySelector('.el-scrollbar__wrap')
            for(let i=0; i<point.length; i++){
                this.pList.push(point[i].offsetTop-1);
            };
            //滚动条滚动事件
            scrollWrap.addEventListener('scroll',(e)=>{
                let top = scrollWrap.scrollTop;
                let num = this.pList.length;
                for(let i=0; i<num; i++){
                    if(top > this.pList[i] && top < this.pList[(i+1)]){
                        this.navActive = i;
                        break;
                    }
                    if(top > this.pList[(num-1)]){
                        this.navActive = i;
                    }
                }
            });
        }
    },
    mounted(){
        this.trendCurveChart();
        this.nodeCoordinates();
    }
    
}
</script>
<style scoped lang="less">
.alarm-info-box{
    height: 100%;
    .toolbar-box{
        padding: 8px;
    }
    .tip-text-box{
        height: 30px;
        line-height: 30px;
        padding: 0 10px;
        background: #E9ECF0;
        .icon-tishi{
            color: #166BCE;
            display: inline-block;
            vertical-align: middle;
        }
        .toolbar-btn{
            float: right;
            color: #166BCE;
            cursor: pointer;
            opacity: 0.8;
            &:hover{
                opacity: 1;
            }
        }
    }
    .info-content-wrap{
        height: 464px;
        padding: 8px 0 8px 8px;
        .left-content{
            width: 150px;
            float: left;
            height: 100%;
            border: 1px solid #dce9fa;
            margin-right: 8px;
            position: relative;
            .nav-list{
                height: calc(100% - 32px);
                width: 1px;
                background: #c3c8ce;
                position: absolute;
                top: 16px;
                left: 32px;
                &>li{
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    border: 1px solid #c3c8ce;
                    white-space: nowrap;
                    margin-bottom: 54px;
                    background: #fff;
                    margin-left: -5px;
                    line-height: 5px;
                    text-indent: 18px;
                    position: relative;
                    &:before{
                        content:'';
                        position: absolute;
                        width: 6px;
                        height: 6px;
                        border-radius: 50%;
                        background: #c3c8ce;
                        top: 1px;
                        left: 1px;
                    }
                    &>span{
                        cursor: pointer;
                        &:hover{
                            color: #166bce;
                        }
                    }
                }
                &>li:first-child{
                    margin-bottom: 32px;
                     &:before{
                         content:'';
                         background: #fff;
                     }
                }
                &>li:last-child{
                    position: absolute;
                    left: 1px;
                    bottom: 0;
                    margin-bottom: 0;
                     &:before{
                         content:'';
                         background: #fff;
                     }
                }
                &>li.active{
                    border: 1px solid #166bce;
                    &:before{
                        background: #166bce;
                    }
                }
            }
        }
        .right-content{
            width: calc(100% - 158px);
            float: left;
            height: 100%;
            .content-box{
                width: calc(100% - 8px);

            }
            .base-info-list{
                padding: 10px;
                li{
                    float: left;
                    width: 33%;
                    padding-left: 14px;
                    font-size: 12px;
                    line-height: 30px;
                    &::before{
                        content: "";
                        display: inline-block;
                        vertical-align: middle;
                        margin-bottom: 3px;
                        width: 7px;
                        height: 7px;
                        background: #c7d8ee;
                        margin-right: 6px;
                    }
                }
            }
            #trend-curve-chart{
                height: 308px;
            }
        }
    }
}
.modular-box{
    border: 1px solid #dce9fa;
    margin-bottom: 8px;
    &:last-child{
        margin-bottom: 0;
    }
    .modular-head{
        height: 36px;
        line-height: 34px;
        border-bottom: 1px solid #dce9fa;
        padding-left: 16px;
        font-size: 14px;
        &>span{
            display: block;
            &::before{
                content:'';
                display: inline-block;
                vertical-align: middle;
                width: 3px;
                height: 10px;
                background: #166BCE;
                margin-right: 10px;
            }
        }
    }
    .modular-content{
        min-height: 50px;
        .modular-toolbar-wrap{
            padding: 2px 0 8px 10px;
        }
    }
}
</style>


