                        <div class="pb">
                            <p><span>工作状态：</span> 标样核查 加热</p>
                            <p><span>报警信息：</span> 试剂余量不足 </p>
                            <p><span>标定时间：</span> 2019-01-01 </p>
                            <p><span>试剂剩余次数：</span> 5次 </p>
                            <p><span>显色剂：</span> 1000ml </p>
                            <p><span>掩蔽剂：</span> 1000ml </p>
                            <p><span>蒸馏水：</span> 1000ml </p>
                            <p><span>清洗剂：</span> 1000ml </p>
                        </div>
                        <div class="select">
                                <div class="layoutBox" style="width:126px">
                                  <el-select v-model="typeSelect" collapse-tags placeholder="请选择">
                                    <el-option
                                      v-for="item in optionData"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value"
                                    ></el-option>
                                  </el-select>
                                </div>&emsp;
                                <el-button type="secondBtn">远程反控</el-button>
                        </div>
                    </li>
                    
                </ul>
               
            </div>
          </div>
        </div>
      </div>
      <div class="model">
            <div class="modelTitle">集成远程反控</div>
            <div class="modelBody" style="padding:8px">
                  <div class="jcBox">
                        <p class="jc">系统集成</p>
                        <p>运行  五参数留样</p>
                        <span>开机时间: 2019-07-29 10:10:10&emsp; &emsp; </span><el-button type="secondBtn">重启</el-button><el-button type="secondBtn">提取</el-button><el-button type="secondBtn">校时</el-button><el-button type="secondBtn">远程控制系统进入待机</el-button><el-button type="secondBtn">远程重启系统单元测试</el-button><el-button type="secondBtn">远程反控</el-button>
                  </div>
                  <div class="dyBox clearfix">
                        <div class="block bOne">
                            <p>控制单元设置</p>
                            <div><span class="bSpan">运行模式 :  </span>&emsp;<div class="layoutBox" style="width:27%">
                                  <el-select v-model="typeSelect" collapse-tags placeholder="请选择内容">
                                    <el-option
                                      v-for="item in optionData"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value"
                                    ></el-option>
                                  </el-select>
                                </div>&emsp; <el-button type="secondBtn">提取</el-button><el-button type="secondBtn">设置</el-button>
                            </div>
                            <div><span class="bSpan">修改密码 :  </span>&emsp;<div class="layoutBox" style="width:27%">
                                  <el-input placeholder="请输入"></el-input>
                                </div>&emsp; <el-button type="secondBtn">设置</el-button>
                            </div>
                            <div><span class="bSpan">运行周期 :  </span>&emsp;<div class="layoutBox" style="width:27%">
                                  <el-select v-model="typeSelect" collapse-tags placeholder="请选择内容">
                                    <el-option
                                      v-for="item in optionData"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value"
                                    ></el-option>
                                  </el-select>
                                </div>&emsp; <el-button type="secondBtn">设置</el-button>
                            </div>
                            <div><span class="bSpan">实时数据间隔 :  </span>&emsp;<div class="layoutBox" style="width:27%">
                                 <el-input-number v-model="duration" controls-position="right" :min="0"></el-input-number>
                                </div> 分钟&emsp; <el-button type="secondBtn">提取</el-button><el-button type="secondBtn">设置</el-button>
                            </div>
                            <div style="margin:0px;"><span class="bSpan">超时重发间隔 :  </span>&emsp;<div class="layoutBox" style="width:11.5%">
                                 <el-input-number v-model="duration" controls-position="right" :min="0"></el-input-number>
                                </div>&emsp;秒&emsp;<div class="layoutBox" style="width:11.5%">
                                 <el-input-number v-model="duration" controls-position="right" :min="0"></el-input-number>
                                </div>&nbsp; 次&nbsp;&nbsp;&nbsp;&emsp;<el-button type="secondBtn">设置</el-button>
                            </div>
                        </div>
                         <div class="block bTwo">
                            <p>质控设置</p>
                            <div><span class="bSpan">监测参数 :  </span>&emsp;<div class="layoutBox" style="width:210px">
                                  <el-select v-model="typeSelect" collapse-tags placeholder="请选择内容">
                                    <el-option
                                      v-for="item in optionData"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value"
                                    ></el-option>
                                  </el-select>
                                </div>&emsp; <el-button type="secondBtn">设置</el-button>
                            </div>
                             <div><span class="bSpan">加标体积 :  </span>&emsp;<div class="layoutBox" style="width:180px">
                                 <el-input placeholder="请输入"></el-input> 
                                </div> ml
                            </div>
                             <div style="text-align:center">
                                 <el-button type="secondBtn">加标回收</el-button>
                              </div>
                             <p>留样器控制</p>
                              <div style="text-align:center">
                                 <el-button type="secondBtn">手动远程留样</el-button>
                              </div>
                        </div>
                         <div class="block bThree">
                            <p>数据补采</p>
                            <div><span class="bSpan">开始时间 :  </span>&emsp;<div class="layoutBox" style="width:210px">
                                  <el-date-picker
                                    v-model="value"
                                    type="datetime"
                                    placeholder="选择日期时间">
                                  </el-date-picker>
                                </div>
                            </div>
                            <div><span class="bSpan">结束时间 :  </span>&emsp;<div class="layoutBox" style="width:210px">
                                   <el-date-picker
                                    v-model="value"
                                    type="datetime"
                                    placeholder="选择日期时间">
                                  </el-date-picker>
                                </div>
                            </div>
                            <div><span class="bSpan">数据类型 :  </span>&emsp;<div class="layoutBox" style="width:210px">
                                  <el-select v-model="typeSelect" collapse-tags placeholder="请选择内容">
                                    <el-option
                                      v-for="item in optionData"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value"
                                    ></el-option>
                                  </el-select>
                                </div>
                            </div>
                            <div style="text-align:center">
                                 <el-button type="secondBtn">采集</el-button>
                            </div>
                        </div>
                  </div>
            </div>
      </div>
       <div class="model">
          <div class="modelTitle">反控记录</div>
          <div class="modelBody" style="padding:8px">
            <el-table
              :data="tableData"
              border
              height="100%">
               <el-table-column
                type="index"
                label="序号">
              </el-table-column>
              <el-table-column
                prop="cn"
                label="请求编码">
              </el-table-column>
              <el-table-column
                prop="description"
                label="命名描述">
              </el-table-column>
              <el-table-column
                prop="operationUser"
                label="操作用户">
              </el-table-column>
              <el-table-column
                prop="operationTime"
                label="时间">
              </el-table-column>
              <el-table-column
                prop="ip"
                label="IP">
              </el-table-column>
              <el-table-column
                prop="response"
                label="请求应答">
              </el-table-column>
              <el-table-column
                prop="result"
                label="执行结果">
              </el-table-column>
              <el-table-column
                prop="content"
                label="内容">
              </el-table-column>
            </el-table>
             <div class="paginationBox">
            <el-pagination
              @size-change="paginationSizeChange"
              @current-change="paginationChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              background
              :small = "true"
              popper-class = "select-common-option"
              :page-sizes = "pageList"
              layout="total,sizes,slot,->,prev, pager, next, jumper"
              :total="totalRecords">
              <span class="el-pagination__current">当前第{{currentPage}}/{{totalPage}}页</span>
            </el-pagination>
          </div>
          </div>
      </div>
    </el-scrollbar>
  </div>
</template>
<script>
export default {
  name: "",
  data() {
    return {
      optionData: [],
      typeSelect: "",
      tableData: [],
      duration: "",
      value: "",
      currentPage: 1, //分页组件当前页
      pageSize: 10, //分页每页多少条数据
      totalRecords: 0, //总条数
      pageList: [10, 15, 20], //设置每页多少条
      positionNum:0,
      allLiwidth:0,
      list:[{},{},{},{},{},{},{},{},{},{},],
    };
  },
  methods: {
    leftClick(){
        if(this.positionNum==0){
           return false;
        }
        this.positionNum=this.positionNum-255;
        
    },
    rightClick(){
         var ele = document.getElementById("carBody");
         var w = ele.clientWidth || ele.offsetWidth;
         if(w + this.positionNum > this.allLiwidth){
           return false;
         }
         this.positionNum=this.positionNum+255;
    }
  },
  mounted(){
     this.allLiwidth=this.list.length*270;
  }
};
</script>
<style scoped lang='less'>
.rcBox {
  background: #eff3f5;
  height: 100%;
  .statusBox {
    height: 214px;
    background: #fff;
    border: 1px solid #dce9fa;
    padding-left: 270px;
    position: relative;

    .videoBox {
      width: 242px;
      height: 196px;
      position: absolute;
      left: 8px;
      top: 8px;
    }
    .statusMes {
      height: 100%;
      > div {
        height: 32px;
        font-size: 13px;
        > span {
          color: #666;
          font-size: 13px;
          height: 32px;
          display: inline-block;
          width: 112px;
          text-align: left;
        }
      }
      .station {
        font-size: 16px;
        font-weight: 700;
        height: 42px;
        margin-top: 42px;
      }
    }
  }
  .model {
    border: 1px solid #dce9fa;
    background: #fff;
    margin-top: 6px;
    .modelTitle {
      height: 38px;
      line-height: 38px;
      border-bottom: 1px solid #dce9fa;
      padding-left: 25px;
      position: relative;
      &:before {
        content: "";
        width: 3px;
        height: 10px;
        position: absolute;
        background: #166bce;
        left: 14px;
        top: 14px;
      }
    }
    .modelBody {
      .carBox{
         padding: 30px 60px;
         height: 352px;
         position: relative;
         >.btnDiv{
             width: 36px;
             height: 36px;
             border-radius: 50%;
             background:rgba(13,37,64,0.25);
             position: absolute;
             top: 50%;
             transform: translateY(-18px);
             cursor: pointer;
             line-height: 36px;
             text-align: center;
             &:hover{
               background: rgba(23,67,115,0.32);
             }
             >i{
               color: #fff;
               font-size: 16px;
             }
           }
           .leftBtn{
              left:15px;
           }
           .rightBtn{
              right: 15px;
           }
         .carBody{
            
           height: 305px;
           position: relative;
           overflow: hidden;
           
         }
      }
      .factorBox {
        
        
        position: absolute;
        left: 0px;
        top: 0px;
        width: 3000px;
        transition: 0.6s all;
        li {
          height: 284px;
          margin-right: 15px;
          width: 255px;
          border-radius: 8px;
          border: 1px solid #a0c3eb;
          box-shadow: 5px 5px 0px #dde9f8;
          float: left;
          &:last-child {
            margin-right: 0px;
          }
          .tp {
            height: 38px;
            line-height: 38px;
            border-bottom: 1px solid #d9e6f7;
            font-size: 16px;
            font-weight: 700;
            text-align: center;
          }
          .pb {
            padding: 10px;
            padding-bottom: 3px;
            p {
              height: 23px;
              padding-left: 15px;
              position: relative;
              span {
                color: #666;
              }
              &:before {
                content: "";
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background: #166bce;
                position: absolute;
                left: 2px;
                top: 8px;
              }
            }
          }
          .select {
            height: 48px;
            margin: 0 12px;
            border-top: 1px dashed #d9e6f7;
            padding-top: 5px;
            line-height: 35px;
            .layoutBox {
              display: inline-block;
            }
          }
        }
      }
      .jcBox {
        border: 1px solid #dce9fa;
        padding: 16px;
        padding-left: 50px;
        .jc {
          position: relative;
          &:before {
            content: "";
            position: absolute;
            left: -30px;
            top: 0px;
            width: 24px;
            height: 24px;
            background: url("../../assets/images/index/xitongjicheng_icon.png");
          }
        }
        > p:first-child {
          height: 33px;
          font-size: 14px;
          font-weight: 700;
        }
        > p:nth-child(2) {
          height: 27px;
        }
      }
      .dyBox {
        margin-top: 10px;
        .block {
          border: 1px solid #dce9fa;
          float: left;
          padding: 16px;
          > p {
            height: 30px;
            font-size: 16px;
            padding-left: 15px;
            position: relative;
            &:before {
              content: "";
              width: 7px;
              height: 7px;
              background: #c7d8ee;
              position: absolute;
              left: 0px;
              top: 7px;
            }
          }
          > div {
            margin-bottom: 12px;
          }
          .bSpan {
            display: inline-block;
            width: 89px;
            text-align: right;
          }
        }
        .bOne {
          margin-right: 10px;
          width: calc(100% - 791px);
        }
        .bTwo {
          width: 425px;
          margin-right: 10px;
          height: 262px;
        }
        .bThree {
          width: 346px;
          height: 262px;
        }
      }
    }
  }
}
</style>




