<script>
export default {
    name:'alarmHand',
    data(){
        return{
            navList:[{
                name:'报警诊断',
            },{
                name:'报警审核',
            },{
                name:'现场核实',
            },{
                name:'核实审核',
            }],
            navActive: 0,
            // 报警诊断
            diagnosisForm:{
                user:'',
                time:'',
                conclusion:'',
                describe:'',
                enclosure:[]
            },
            diagnosisRules:{

            },
            // 报警审核表单数据
            alarmAuditForm:{
                time:'',
                conclusion:'',
                explain:'',
                enclosure:[]
            },
            alarmAuditRules:{

            },
            // 现场核实表单数据
            siteTreatmentForm:{
                explain:'',
                enclosure:[]
            },
            siteTreatmentRules:{

            },
            // 核实审核表单数据
            processingAuditForm:{
                conclusion:'1',
                explain:'',
                enclosure:[]
            },
            processingAuditRules:{

            }
        }
    },
    methods:{
        nextStep(){
            if((this.navList.length-1) !== this.navActive){
                this.navActive++
            }
        }
    },
    mounted(){

    }
    
}
</script>
<style scoped lang="less">
.alarm-hand-box{
    height: 541px;
    padding: 8px;
    .alarm-hand-left{
        width: 150px;
        height: 100%;
        float: left;
        border: 1px solid #dce9fa;
        .navList{
            padding-top: 36px;
            &>li{
                width: 2px;
                height: 114px;
                background: #d1def0;
                white-space: nowrap;
                margin-left: 30px;
                position: relative;
                &:last-child{
                    height: 2px;
                }
                &>span{
                    color: #cfcfcf;
                    position: absolute;
                    top: -6px;
                    left: 10px;
                }
                &:before{
                    content: '';
                    position: absolute;
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: #d1def0;
                    top: 0px;
                    left: -3px;
                }
            }
            &>li.active{
                &>span{
                    color: #166bce;
                    font-weight: 600;
                }
                &:before{
                   width: 4px;
                   height: 4px;
                   background: #fff;
                   border: 2px solid #166bce;
                }
                
            }
             &>li.obsolete{
                background: #36c25e;
                &:before{
                    background: #36c25e;
                }
                &>span{
                    color: #1a1a1a;
                    cursor: pointer;
                }
             }
        }
    }
    .alarm-hand-right{
        width: calc(100% - 158px);
        height: 100%;
        float: right;
        .alarm-page{
            height: 100%;
        }
    }
}

.modular-box{
    border: 1px solid #dce9fa;
    height: 100%;
    &:last-child{
        margin-bottom: 0;
    }
    .modular-head{
        height: 36px;
        line-height: 34px;
        border-bottom: 1px solid #dce9fa;
        padding-left: 16px;
        font-size: 14px;
        &>span{
            display: block;
            &::before{
                content:'';
                display: inline-block;
                vertical-align: middle;
                width: 3px;
                height: 10px;
                background: #166BCE;
                margin-right: 10px;
            }
        }
    }
    .modular-content{
        min-height: 50px;
        height: calc(100% - 36px);
        .modular-toolbar-wrap{
            padding: 2px 0 8px 10px;
        }
        .btn-wrap{
            text-align: center
        }
    }
}
.normal-table-wrap{
    padding: 8px;
    table{
        width: 100%;
        border-collapse: collapse;
        th,td{
            border: 1px solid #d0dded;
            padding: 5px 8px;
            .file-span{
                cursor: pointer;
                color: #166BCE;
                opacity: 0.8;
                &:hover{
                    opacity: 1;
                }
            }
        }
    }
}
.alarm-audit-form{
    height: 264px;
}
.el-row{
    margin-bottom: 8px;
    padding: 10px 0;
    .el-form-item__content{
        line-height: 30px;
    }
}
</style>


